// Drupal variables
$mobile: 640px;
$tablet: 768px;
$desktop: 1030px;
$wide: 1290px;
$jumbotron: 1380px;

$tabletPadding: 748px;
$widePadding: 1260px;

$max-vw: $wide;

$mq-responsive: true;

$mq-breakpoints: (mobile: 640px,
  tablet: 768px,
  desktop: 1030px,
  wide: 1290px,
  mobile-menu: 1320px);

$mq-static-breakpoint: desktop;
$mq-show-breakpoints: (mobile, tablet, desktop, wide, mobile-menu);

@import "../vendor/mq";
